import Grid from "@material-ui/core/Grid";
import frameBackground from "assets/images/landing/frame1.jpg";
import WithPadding from "components/shared/WithPadding";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import style from "./frame.module.scss";

const heading = "High School Students: Rise";
const paragraph = "Discover your innate strengths and pave the way for a future you'll love. Tailored insights to guide your academic and early career choices.";
const buttonText = "Rise Now";
const buttonLink = "/auth/register";
const buttonHoverText = "Start the Test";
const backgroundRightStyle = {
  backgroundImage: `
    linear-gradient(-90deg, rgba(63, 212, 238, 0.1) 28.96%, rgba(75, 155, 238, 0.1) 74.9%),
    linear-gradient(-90deg, rgba(255, 255, 255, 0.5) 28.96%, rgba(255, 255, 255, 0) 51.12%),
    linear-gradient(-90deg, rgba(255, 255, 255, 0.5) 28.96%, rgba(255, 255, 255, 0) 51.12%),
    linear-gradient(-90deg, rgba(255, 255, 255, 0.5) 28.96%, rgba(255, 255, 255, 0) 51.12%), 
    url(${frameBackground})`,
  backgroundRepeat: 'no-repeat',
  backgroundPosition: '50%',
  backgroundSize: 'cover',
  backgroundAttachment: 'fixed',
};


export default function Frame1() {
  const [hover, setHover] = useState(false);
  const navigate = useNavigate();

  return (
    <WithPadding className={style.wrapper} style={backgroundRightStyle}>
      <div className={style.mobGradient}>

        <Grid container className={style.right}>
          <Grid item xs={12} sm={12} md={4} lg={4}></Grid>
          <Grid item xs={12} sm={12} md={4} lg={4}></Grid>
          <Grid item xs={12} sm={12} md={4} lg={4}>
            <div className={style.heading}>{heading}</div>
            <div className={style.paragraph}>{paragraph}</div>
            <button
              type="button"
              className={style.button}
              onClick={() => navigate(buttonLink)}
              onMouseEnter={() => setHover(true)}
              onMouseLeave={() => setHover(false)}
            >
              {hover ? buttonHoverText : buttonText}
            </button>
          </Grid>
        </Grid>
      </div>
    </WithPadding>
  );
}
